<template>
    <div class="bubble-tips-container" :style="{top: top, left: left}">
        <div class="bubble-content row" v-show="showBubble" v-for="item in currentList" :key="item.id">
            <img class="bubble-img" :src="item.user.avatar" />
            <div class="xs">
                {{item.template}}
            </div>
        </div>
    </div>
</template>

<script>
import { getBubbleLists } from "@API/app";
export default {
    name: "BubbleTips",
    props: {
        // 是否停止切换
        discharge: {
            type: Boolean,
            default: false
        },
        top: {
            type: String,
            default: "20px"
        },
        left: {
            type: String,
            default: "10px"
        },
        updateTime: {
            type: Number,
            default: 5 * 60 * 1000
        }
    },
    data() {
        return {
            index: this.$ls.get("currentIndex") || 0,
            list: [],
            currentList: [],
            timer: null,
            showBubble: false,
        }
    },
    watch: {
        index(newValue, oldValue) {
            if(this.index - this.list.length >= 0) {
                this.showBubble = false;
                let timer = setTimeout(() => {
                    this.$ls.set("currentIndex", 0)
                    if(this.timer) {
                        clearInterval(this.timer)
                        this.timer = null
                    }
                    this.fadeUpBubble();
                    clearTimeout(timer)
                }, 2000)
            } else {
                if(this.timer) {
                    clearInterval(this.timer)
                    this.timer = null
                }
                this.fadeUpBubble();
                return;
            }
        },
        discharge() {
            // 为了让活性的页面停止计时器
            if(this.discharge) {
                // 停止
                this.$ls.set("currentIndex", this.index);
                clearInterval(this.timer);
                this.timer = null;
                return false;
            } else {
                // 继续切换
                let currentInex = this.$ls.get("currentInex") || this.list.length;
                if(currentInex - this.list.length < 0) {
                    if(this.timer) {
                        setInterval(this.timer)
                        this.timer = null;
                    }
                    this.fadeUpBubble()
                }
            }
        }
    },
    methods: {
        $getBubbleLists() {
            getBubbleLists().then(res => {
                if(res.code == 1) {
                    this.list = res.data.lists;
                    var requestTime = res.data.time * 1000;
                    this.$ls.set("bubbleList", JSON.stringify(this.list));
                    this.$ls.set("requestTime", requestTime);
                    if(this.timer) {
                        clearInterval(this.timer);
                        this.timer = null;
                    }
                    this.fadeUpBubble()
                }
            })
        },
        fadeUpBubble() {
            let requestTime = this.$ls.get("requestTime");
            let currentTime = new Date();
            this.showBubble = true;
            this.index = this.$ls.get("currentIndex") || 0;
            this.list = this.$ls.get("bubbleList") ? JSON.parse(this.$ls.get("bubbleList")) : [];
            if(currentTime.getTime() - requestTime >= this.updateTime) {
                this.$getBubbleLists();
                this.$ls.set("currentIndex", 0)
                return;
            }
            this.timer = setInterval(() => {
                this.currentList = this.list.slice(this.index, this.index + 1);
                this.$ls.set("currentIndex", ++ this.index);
            }, 4000);
        }
    },
    created() {
        var index = this.$ls.get("currentIndex") || 0;
        var requestTime = this.$ls.get("requestTime");
        var currentTime = new Date();
        var currentList = this.$ls.get("bubbleList") ? JSON.parse(this.$ls.get("bubbleList")) : [];
        if(currentList.length <= 0) {
            this.$getBubbleLists();
            this.$ls.set("currentIndex", 0)
        }
        else if (index - currentList.length >= 0) {
            this.$ls.set("currentIndex", 0);
            if(this.timer) {
                clearInterval(this.timer)
                this.timer = null
            }
            this.fadeUpBubble();
        }
        else if(currentTime.getTime() - requestTime >= this.updateTime) {
            this.$getBubbleLists();
            this.$ls.set("currentIndex", 0)
        } else {
            if(this.timer) {
                clearInterval(this.timer);
                this.timer = null;
            }
            this.fadeUpBubble()
        }
    },
    mounted() {
    },
    destroyed() {
        if(this.timer) {
            clearInterval(this.timer);
            this.timer = null
        }
    }
}
</script>

<style lang="scss" scoped>
    .bubble-tips-container {
        position: absolute;
        z-index: 98;
        .bubble-content {
            padding: 2px 10px 2px 5px;
            background-color: rgba(0, 0, 0, 0.7);
            color: white;
            border-radius: 60px;
            .bubble-img {
                width: 25px;
                height: 25px;
                border-radius: 50%;
                margin-right: 5px;
            }
        }
    }
</style>