<template>
    <div class="home-coupon">
        <div class="coupon-header row-between" @click="goPage('couponCenter')">
            <div class="normal lg title bold">每日领券</div>
            <div class="row">
                <div class="muted sm mr5">进入领券中心</div>
                <img
                    src="@A/images/arrow_right.png"
                    style="width: 14px; height: 14px"
                />
            </div>
        </div>
        <div
            class="coupon-contain row"
            ref="couponContain"
            style="touch-action: pan-y"
        >
            <div class="coupon-wrap row" ref="couponWrap">
                <div
                    class="coupon-item row-between"
                    v-for="item in lists"
                    :key="item.id"
                >
                    <div class="coupon-left">
                        <div class="row info">
                            <div class="md money-logo">¥</div>
                            <price-slice
                                firstClass="home-coupon-size"
                                :price="item.money"
                            ></price-slice>
                            <div
                                class="sm ml5"
                                style="
                                    align-self: flex-end;
                                    line-height: 25px;
                                    margin-bottom: 1.5px;
                                "
                            >
                                优惠券
                            </div>
                        </div>
                        <div class="desc xs row">{{ item.use_condition }}</div>
                    </div>
                    <div
                        class="coupon-right xs primary column-center"
                        @click="getCoupon(item.is_get, item.id)"
                    >
                        立即领取
                    </div>
                    <img
                        v-if="item.is_get == 1"
                        class="coupon-img"
                        src="@A/images/img_receive.png"
                    />
                </div>
            </div>
        </div>
        <div
            class="bg-white row-center progress-container"
            v-show="lists.length >= 3"
        >
            <progress-bar
                progressBarColor="#FF636E"
                :left="progressPer"
            ></progress-bar>
        </div>
    </div>
</template>

<script>
import ProgressBar from "@C/ProgressBar";
import BScroll from "@better-scroll/core";
export default {
    name: "HomeCoupon",
    components: {
        ProgressBar,
    },
    props: {
        lists: {
            type: Array,
            default: () => [],
        },
    },
    watch: {
        lists: {
            immediate: true,
            handler(value) {
                setTimeout(() => {
                    let { couponContain, couponWrap } = this.$refs;
                    this.couponContain = new BScroll(couponContain, {
                        scrollX: true,
                        eventPassthrough: "vertical",
                        probeType: 3,
                        bounce: false,
                        cancelable: false,
                    });
                    if (this.couponContain) {
                        this.couponContainWidth = couponContain.clientWidth;
                        this.couponWrapWidth = couponWrap.clientWidth;
                    }
                });
            },
        },
        couponContain(value) {
            this.couponContain.on(
                "scroll",
                (pos) => {
                    this.progressPer =
                        (Math.abs(pos.x) /
                            (this.couponWrapWidth - this.couponContainWidth)) *
                        100;
                },
                this
            );
        },
    },
    data() {
        return {
            couponContain: null,
            couponContainWidth: 0,
            couponWrapWidth: 0,
            progressPer: 0,
        };
    },
    methods: {
        goPage(name) {
            this.$router.push({ name: name });
        },
        getCoupon(is_get, id) {
            this.$emit("GET_COUPON", id);
        },
    },
};
</script>

